<!-- /login -->
<template>
  <div class="page-header clear-filter" filter-color="orange">
    <div
      class="page-header-image"
      style="background-image: url('img/login.jpg')"
    ></div>
    <div class="content">
      <div class="container">
        <div class="col-md-5 ml-auto mr-auto">
          <card type="login" plain>
            <div slot="header" class="logo-container">
              <img v-lazy="'img/TTT-logo.png'" alt="" />
            </div>

            <!--   -->
            <fg-input
              v-model="email"
              class="no-border input-lg"
              addon-left-icon="now-ui-icons ui-1_email-85"
              placeholder=""
              type="email"
            >
            </fg-input>

            <!--   -->
            <fg-input
              v-model="pw"
              @keypress.enter="login"
              class="no-border input-lg"
              addon-left-icon="now-ui-icons ui-1_lock-circle-open"
              placeholder=""
              type="password"
              autocomplete="off"
            >
            </fg-input>

            <!--    -->
            <template slot="raw-content">

              <!--   -->
              <div class="card-footer text-center">
                <n-button
                  @click="login"
                  type="primary"
                  block
                  round
                >
                  
                </n-button>
              </div>

              <!--   -->
              <div class="card-footer text-center">
                <n-button
                  @click.native="modals.signup = true"
                  type="info"
                  block
                  round
                >
                  TTT's   <i class="now-ui-icons sport_user-run"></i>
                </n-button>
              </div>

              <modal :show.sync="modals.signup" headerClasses="justify-content-center">
                <signup-form/>
              </modal>

            </template>
          </card>
        </div>
      </div>
    </div>
    <main-footer></main-footer>
  </div>
</template>
<script>
import { mapActions } from "vuex";
import { Card, Button, FormGroupInput, Modal } from '@/components';
import MainFooter from '@/layout/MainFooter';
import SignupForm from '@/pages/components/SignupForm';
import firebase from 'firebase';

export default {
  name: 'login',
  bodyClass: 'login-page',
  components: {
    Card,
    MainFooter,
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput,
    Modal,
    SignupForm
  },
  data(){
    return {
      email:'',
      pw:'',
      modals: {
        signup: false,
        help: false
      }
    }
  },
  methods: {
    ...mapActions("user", ["getUserInfo"]),

    // 
    async login() {

      // firebase 
      await firebase.auth()
                    .signInWithEmailAndPassword(this.email, this.pw)
                    .then(
                      user => {
                        //   uid 
                        localStorage.setItem("user", user.user.uid);
                        //    
                        firebase.auth().currentUser.getIdToken().then(idToken => {
                          localStorage.setItem("user_token", idToken);
                        });
                      },
                      function(err) {
                        alert(": " + err.message);
                      }
                    );

      //    
      if(localStorage.getItem("user_token")) {
        await this.getUserInfo();     // user state userInfo 
        this.$router.push("/");   //  
      }

    },
  },
};
</script>
<style></style>
